<script>
import { ROUTE_NAME } from '@/constants/route.constants'
import { MEMBER_DETAIL, MEMBER_WALLET, MEMBER_VIP_TIER_PROGRESS, MEMBER_GET_VIP_PROGRESS, MEMBER_VIP_TIER_COMMUNICATION } from '@/store/member.module'
import { locale, errorCodeHelper, uiHelper } from '@/util'
import { AFFILIATE_ONE_TIME_BONUS_DETAIL } from '@/store/affiliate.bonus.module'
import { REWARD_CONFIGURATION_STATUS } from '@/store/reward.module'
import WatchTutorialVideo from "@/components/WatchTutorialVideo.vue";

export default {
  name: 'ProfileSheet',
  components: {WatchTutorialVideo},
  props: {
    showProfileSheet: {
      type: Boolean,
      required: true,
      default: false
    },
    closeProfileSheet: {
      type: Function,
      required: false
    },
    logout: {
      type: Function,
      required: false
    }
  },
  data: () => ({
    currentDeposit: 0,
    requiredDeposit: 0,
    progressRequired: 0,
    turnoverRequired: 0,
    nextlevelname: '',
    tierName: '',
    currentProgressPercentage: 0,
    tierColorThemeName: 'starter',
    routeName: ROUTE_NAME,
    notLoading: true,
    currentCurrency: uiHelper.getCurrency(),
    showBalance: true,
    isAndroid: uiHelper.isAndroid(),
    isDesktop: uiHelper.getPlatform() == 'desktop',
    communicationUrl: ''
  }),
  created() {
    if (this.isLoggedIn) {
      this.getMemberDetail()
      this.getVipTierProgress()
      this.getVipProgress()
      this.getConfigurationStatus()
    }
  },
  computed: {
    hasUnreadInboxMsg() {
      return this.$store.state.inbox.inboxMsg.filter(m => !m.has_read).length > 0
    },
    vipTierUpgrade() {
      return this.$store.state.member.vipTierProgress.upgrade
    },
    memberVipTierProgress() {
      return this.$store.state.member.vipTierProgress.pairing
    },
    isLoggedIn() {
      return this.$store.state.member.isLoggedIn
    },
    memberInfo() {
      return this.$store.state.member.info
    },
    memberWallet() {
      return this.$store.state.member.walletBalance
    },
    affiliateBonus() {
      return this.$store.state.affiliateBonus.affiliateBonusDetail
    },
    communicationLink() {
      return this.$store.state.member.vipTierCommunication.success
    },
    configuration() {
      return this.$store.state.reward.configurationStatus
    }
  },
  methods: {
    getConfigurationStatus() {
      let filter = {
        currency: uiHelper.getCurrency()
      }
      this.$store.dispatch(`${REWARD_CONFIGURATION_STATUS}`, { filter })
    },
    getVipTierProgress() {
      this.$store.dispatch(MEMBER_VIP_TIER_PROGRESS)
    },
    getVipProgress() {
      if (this.isLoggedIn) {
        this.$store.dispatch(MEMBER_GET_VIP_PROGRESS)
      }
    },
    goToTierDetails() {
      this.$router.push({ name: this.routeName.VIPTIER, params: { lang: this.$route.params.lang } })
      this.closeProfileSheet()
    },
    getAffiliateBonusDetail() {
      this.$store.dispatch(`${AFFILIATE_ONE_TIME_BONUS_DETAIL}`)
    },
    maskBalance() {
      this.showBalance = !this.showBalance
    },
    getMemberDetail() {
      this.$store.dispatch(`${MEMBER_DETAIL}`)
    },
    getMemberWallet() {
      const lastDispatchTime = sessionStorage.getItem('lastDispatchTime')

      if (!lastDispatchTime || Date.now() - parseInt(lastDispatchTime) > 15 * 1000) {
        this.notLoading = false
        sessionStorage.setItem('lastDispatchTime', Date.now()) // Update last dispatch time in sessionStorage
        this.$store.dispatch(MEMBER_WALLET)
      }

      this.maskBalance()
    },
    getVipCommunicationLink(type) {
      let communicationUrl = {
        type: type
      }

      this.$store.dispatch(`${MEMBER_VIP_TIER_COMMUNICATION}`, { communicationUrl })
    },
    logoutBtn() {
      this.logout()
      this.closeProfileSheet()
    },
    routeTo(routeTo, extLink) {
      let params
      this.closeProfileSheet()
      switch (routeTo) {
        case 'REWARD_STORE':
          routeTo = ROUTE_NAME.REWARDSTORE
          params = {
            lang: this.$route.params.lang
          }
          break
        case 'REDEMPTION':
          routeTo = ROUTE_NAME.REDEMPTION_HISTORY
          params = {
            tab: 'REDEMPTION',
            lang: this.$route.params.lang
          }
          break
        case 'DEPOSIT':
          // routeTo = ROUTE_NAME.DEPOSIT
          routeTo = ROUTE_NAME.FUNDS
          params = {
            tab: 'deposit',
            lang: this.$route.params.lang
          }
          break
        case 'WITHDRAWAL':
          routeTo = ROUTE_NAME.FUNDS
          params = {
            tab: 'withdrawal',
            lang: this.$route.params.lang
          }
          break
        case 'INSTANTREBATE':
          routeTo = ROUTE_NAME.REBATE
          params = {
            lang: this.$route.params.lang
          }
          break
        case 'TRANSFER_RECORD':
          routeTo = ROUTE_NAME.TRANSFER_HISTORY
          params = {
            tab: 'TRANSFER',
            lang: this.$route.params.lang
          }
          break
        case 'BETTING_RECORD':
          routeTo = ROUTE_NAME.BETTING_HISTORY
          params = {
            tab: 'TRANSFER',
            lang: this.$route.params.lang
          }
          break
        case 'TURNOVER_RECORD':
          routeTo = ROUTE_NAME.TURNOVER_RECORD
          params = {
            tab: 'RUNNING',
            lang: this.$route.params.lang
          }
          break
        case 'BONUS':
          routeTo = ROUTE_NAME.BONUS_HISTORY
          params = {
            tab: 'REBATE',
            lang: this.$route.params.lang
          }
          break
        case 'TRANSACTION':
          routeTo = ROUTE_NAME.TRANSACTION_HISTORY
          params = {
            tab: 'DEPOSIT',
            lang: this.$route.params.lang
          }
          break
        case 'PERSONAL':
          routeTo = ROUTE_NAME.PERSONAL
          params = {
            lang: this.$route.params.lang
          }
          break
        case 'BANK_DETAIL':
          routeTo = ROUTE_NAME.BANK_ACCOUNT
          params = {
            lang: this.$route.params.lang
          }
          break
        case 'CHANGE_PASSWORD':
          routeTo = ROUTE_NAME.CHANGE_PASSWORD
          params = {
            lang: this.$route.params.lang
          }
          break
        case 'INBOX':
          routeTo = ROUTE_NAME.INBOX
          params = {
            lang: this.$route.params.lang
          }
          break
        case 'REFERRAL_SUMMARY':
          routeTo = ROUTE_NAME.REFERRAL_SUMMARY
          params = {
            lang: this.$route.params.lang
          }
          break
        case 'REFERRAL_BONUS':
          routeTo = ROUTE_NAME.REFERRAL_BONUS
          params = {
            lang: this.$route.params.lang
          }
          break
        case 'REWARDS':
          routeTo = ROUTE_NAME.REWARDS
          params = {
            lang: this.$route.params.lang
          }
          break
        case 'CLAIM_VOUCHER':
          routeTo = ROUTE_NAME.CLAIM_VOUCHER
          params = {
            lang: this.$route.params.lang
          }
          break
        case 'APPLY_AGENT':
          routeTo = ROUTE_NAME.APPLY_AGENT
          params = {
            lang: this.$route.params.lang
          }
          break
        case 'MWOF':
          routeTo = ROUTE_NAME.MWOF
          params = {
            lang: this.$route.params.lang
          }
          break
        case 'WOF':
          routeTo = ROUTE_NAME.WOF
          params = {
            lang: this.$route.params.lang
          }
          break
        case 'COIN_CARNIVAL':
          routeTo = ROUTE_NAME.GRAB_COIN
          params = {
            lang: this.$route.params.lang
          }
          break
        case 'CARD_MANIA':
          routeTo = ROUTE_NAME.CARD_MANIA
          params = {
            lang: this.$route.params.lang
          }
          break
      }

      if (extLink == undefined || !extLink) {
        this.$router.push({
          name: routeTo,
          params
        })
      } else {
        /*external link */
        if (routeTo == 'FACEBOOK') {
          this.getVipCommunicationLink(routeTo)
          // window.open('https://www.facebook.com/Krikya-110943668180913')
        } else if (routeTo == 'WHATSAPP') {
          this.getVipCommunicationLink(routeTo)
          // window.open('https://bit.ly/KRIKYA_CustomerSupport', '_self')
        } else if (routeTo == 'TELEGRAM') {
          this.getVipCommunicationLink(routeTo)
          // window.open('https://t.me/krikya88')
        }
      }
    },
    downloadAPK() {
      uiHelper.downloadAPK()
    },
    disabledCardMania() {
      if (this.configuration.card_mania) {
        return !this.configuration.card_mania.enabled
      }
      return true
    }
  },
  watch: {
    memberWallet() {
      this.notLoading = true
    },
    isLoggedIn() {
      if (!this.isLoggedIn) {
        return
      }
      this.getAffiliateBonusDetail()
      this.getVipTierProgress()
      this.getMemberDetail()
      this.getVipProgress()
    },
    vipTierUpgrade() {
      if (this.vipTierUpgrade != null) {
        if (this.vipTierUpgrade.current_upgrade_deposit > 0) {
          this.currentProgressPercentage = Math.ceil((this.vipTierUpgrade.current_upgrade_deposit / this.vipTierUpgrade.required_upgrade_deposit) * 100)
        } else {
          this.currentProgressPercentage = 0
        }
        let required = Number(this.vipTierUpgrade.required_upgrade_deposit).toLocaleString()
        required = required.replace(/\.00$/, '')
        let current = Number(this.vipTierUpgrade.current_upgrade_deposit).toLocaleString()
        current = current.replace(/\.00$/, '')
        this.currentDeposit = current

        this.requiredDeposit = required
        this.progressRequired = this.vipTierUpgrade.required_upgrade_deposit - this.vipTierUpgrade.current_upgrade_deposit
        if (this.progressRequired <= 0) {
          this.progressRequired = 'Completed'
        } else {
          this.progressRequired = ' ৳ ' + Number(this.progressRequired).toLocaleString()
        }
        this.nextlevelname = this.vipTierUpgrade.vip_level_name.toLowerCase().replace(/^(.)(.*)$/, function(_, firstLetter, restOfWord) {
          return firstLetter.toUpperCase() + restOfWord.toLowerCase()
        })
        this.turnoverRequired = this.vipTierUpgrade.required_upgrade_turnover - this.vipTierUpgrade.current_upgrade_turnover
        if (this.turnoverRequired <= 0) {
          this.turnoverRequired = 'Completed'
        } else {
          this.turnoverRequired = ' ৳ ' + Number(this.turnoverRequired).toLocaleString()
        }
      }
    },
    memberVipTierProgress() {
      let vipTierProgress = this.$store.state.member.vipTierProgress
      if (vipTierProgress.pairing != null) {
        let curPairing = vipTierProgress.pairing
        //progress found
        this.showProgress = true
        switch (curPairing.level_sequence) {
          case 0:
            this.tierColorThemeName = 'starter'
            break
          case 1:
            this.tierColorThemeName = 'club'
            break
          case 2:
            this.tierColorThemeName = 'diamond'
            break
          case 3:
            this.tierColorThemeName = 'heart'
            break
          case 4:
            this.tierColorThemeName = 'spade'
            break
          case 5:
            this.tierColorThemeName = 'ace'
            break
          case 6:
            this.tierColorThemeName = 'raja'
            break
          default:
            this.tierColorThemeName = 'starter'
            this.tierBarBgColor = '#1867c0'
        }
        this.tierBarBgColor = 'viptier' + this.tierColorThemeName
        this.tierName = curPairing.name.toLowerCase().replace(/^(.)(.*)$/, function(_, firstLetter, restOfWord) {
          return firstLetter.toUpperCase() + restOfWord.toLowerCase()
        })
        this.currentLv = this.memberVipTierProgress.level_sequence
        this.nextLv = this.memberVipTierProgress.level_sequence + 1
      } else {
        //no progress
        this.showProgress = false
      }
    },
    communicationLink() {
      if (this.$store.state.member.vipTierCommunication.success) window.open(this.$store.state.member.vipTierCommunication.data)
    },
    configuration() {
      return this.$store.state.reward.configurationStatus
    }
  }
}
</script>

